<template>
  <div class="content-networkTest">
    <headerUsually btnShow="true" titleText="网络测试" url="/mobileOrdering/home"></headerUsually>
    <div class="list">
      <div class="item">
        <div>IP地址</div>
        <input class="input" type="text" v-model="ip" placeholder="请输入IP地址" />
      </div>
      <div class="item">
        <div>端口号</div>
        <input class="input" type="text" v-model="port" placeholder="请输入端口号" />
      </div>
      <div class="item">
        <div>状态</div>
        <span class="status-text">{{status}}</span>
      </div>
    </div>
    <div class="btn" @click="networkTest()">确定</div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      ip: "",
      port: "",
      status: ""
    };
  },
  mounted() {
    let dcgateway = localStorage.getItem("dcgateway")
      ? JSON.parse(localStorage.getItem("dcgateway"))
      : {};
    this.ip = dcgateway.ip;
    this.port = dcgateway.port;

  },
  methods: {
    async networkTest() {
      let ipEXP = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
      if (!ipEXP.test(this.ip.trim())) {
        this.msg("IP地址不正确");
        return;
      }
      let portEXP = /^([0-9]|[1-9]\d|[1-9]\d{2}|[1-9]\d{3}|[1-5]\d{4}|6[0-4]\d{3}|65[0-4]\d{2}|655[0-2]\d|6553[0-5])$/;
      if (!portEXP.test(this.port.trim())) {
        this.msg("端口号不正确");
        return;
      }

      let requestTime = new Date().getTime();
      this.$http
        .get(
          `http://${this.ip.trim()}:${this.port.trim()}/pos/posUnion/ping?testCase`,
          {
            vv: 6,
            timeout: 3000
          }
        )
        .then(res => {
          if (res.status == 200) {
            let responseTime = new Date().getTime();
            var pv = Math.abs(requestTime - responseTime);
            if (pv > 20) {
              this.status = "一般";
            } else {
              this.status = "良好";
            }
          } else {
            this.msg(res.data.errorMsg);
          }
        })
        .catch(err => {
          this.status = "网络不通！";
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less" scoped>
.content-networkTest {
  padding-top: 46px;
  .list {
    background: #ffffff;
    padding-left: 15px;
    margin-bottom: 20px;
    font-size: 14px;
    color: #666666;

    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding: 17px 15px 17px 0;
      .input {
        flex: 1;
        text-align: center;
        color: #000000;
        outline: none;
        border: none;
      }
      .status-text {
        flex: 1;
        text-align: center;
        color: #000000;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



